export default {
		data() {
			return {
				phone:'',
				pwd:"",
				pwd_confirm:"",
			}
		},
		onLoad(e) {
			console.log(e);
			this.phone=e.phone;
		},
		methods: {
			//重置密码
			resetPassword(){
				let that=this;
				if(that.pwd==''){
					that.Tips({ title: "请输入正确的密码"});
					return;
				}
				if(that.pwd_confirm==''){
					that.Tips({ title: "请再次输入密码"});
					return;
				}
				if(that.pwd_confirm!=that.pwd){
					that.Tips({ title: "两次密码不一致,请重新确认"});
					return;
				}
				that.basePost(
					that.U({ c: 'user_api', a: 'edit_user_pass'}),
					{
						phone:that.phone,
						pwd:that.pwd
					},
					function(res) {
						that.Tips({ title: res.msg});
						that.redToLogin();
					},
					function(res) {
						that.Tips({ title: res.msg});
					},
				);
			},
			redToLogin(){
				uni.redirectTo({ 
					url:'/pages/login/pwd_login/pwd_login'
				})
			}
		},
	}
